import articleList from '../components/articleList.vue'
export default {
  components: {
    articleList: articleList
  },
  data () {
    return {
      detail: {
        clock_in_days: 0,
        is_clock: ''
      },                 // 打卡详情
    }
  },
  onLoad() {
    this.getDetail()
  },
  methods: {
    
    getDetail() {
      let that= this
      let params = {
        url: that.$api.clockInPage
      }
      that.$httpRequest(params).then(res => {
        that.detail= res.data
      })
    },
    
    // 去打卡
    goRelease(status) {
      if(status === 'yes') {
        uni.showToast({
          title: '今天已经打过卡了哦',
          icon: 'none'
        })
        return
      }
      uni.navigateTo({
        url: '../index/release/release?type=Clock_in'
      })
    }
  }
}
